        "pk.eyJ1IjoibHVxNzc4IiwiYSI6ImNrMzdsZ3hxaDAwMHEzamxkdTJtcGppc3UifQ.oQP1bg5nLong5oE0dh8GgQ",
      showMarkers: true,
      markerColor: "#BB6BD9",
      theme: "light",
      mapAnimation: "flyTo",
      footer: "",
      chapters: [
        {
          id: "0",
          alignment: "left",
          title: "",
          image: "",
          description:
            "<span class='maptext-header'>Starting from the 1980s.</span> <br> Still controversial as to who came up with the original boba tea, Chun Shui Tang or Hanlin Tea House",
          location: {
            center: [118.089424, 24.479834],
            zoom: 7,
            pitch: 0,
            bearing: 0,
          },
          hide: false,
          onChapterEnter: [],
          onChapterExit: [],
        },
        {
          id: "1",
          alignment: "left",
          title: "",
          image: "img/barchart.png",
          description: "",
          location: {
            center: [-119.2437, 34.0522],
            zoom: 8,
            pitch: 0,
            bearing: 0,
          },
          hide: false,
          onChapterEnter: [],
          onChapterExit: [],
        },
        {
          id: "2",
          alignment: "left",
          title: "",
          image: "img/piechart.png",
          description: "",
          location: {
            center: [-74.006, 40.7128],
            zoom: 11,
            pitch: 0,
            bearing: 0,
          },
          hide: false,
          onChapterEnter: [],
          onChapterExit: [],
        },
      ],
    };
    var layerTypes = {
      fill: ["fill-opacity"],
      line: ["line-opacity"],
      circle: ["circle-opacity", "circle-stroke-opacity"],
      symbol: ["icon-opacity", "text-opacity"],
      raster: ["raster-opacity"],
      "fill-extrusion": ["fill-extrusion-opacity"],
    };

    var alignments = {
      left: "lefty",
      center: "centered",
      right: "righty",
      full: "fully",
    };

    function getLayerPaintType(layer) {
      var layerType = map.getLayer(layer).type;
      return layerTypes[layerType];
    }

    function setLayerOpacity(layer) {
      var paintProps = getLayerPaintType(layer.layer);
      paintProps.forEach(function (prop) {
        var options = {};
        if (layer.duration) {
          var transitionProp = prop + "-transition";
          options = {
            duration: layer.duration,
          };
          map.setPaintProperty(layer.layer, transitionProp, options);
        }
        map.setPaintProperty(layer.layer, prop, layer.opacity, options);
      });
    }

    var story = document.getElementById("story");
    var features = document.createElement("div");
    features.setAttribute("id", "features");

    var header = document.createElement("div");

    if (config.title) {
      var titleText = document.createElement("h1");
      titleText.innerText = config.title;
      header.appendChild(titleText);
    }

    if (config.subtitle) {
      var subtitleText = document.createElement("h2");
      subtitleText.innerText = config.subtitle;
      header.appendChild(subtitleText);
    }

    if (config.byline) {
      var bylineText = document.createElement("p");
      bylineText.innerText = config.byline;
      header.appendChild(bylineText);
    }

    if (header.innerText.length > 0) {
      header.classList.add(config.theme);
      header.setAttribute("id", "header");
      story.appendChild(header);
    }

    config.chapters.forEach((record, idx) => {
      var container = document.createElement("div");
      var chapter = document.createElement("div");

      if (record.title) {
        var title = document.createElement("h3");
        title.className = "maptitle";
        title.innerText = record.title;
        chapter.appendChild(title);
      }

      if (record.image) {
        var image = new Image();
        image.src = record.image;
        chapter.appendChild(image);
      }

      if (record.description) {
        var story = document.createElement("p");
        story.className = "maptext";
        story.innerHTML = record.description;
        chapter.appendChild(story);
      }

      container.setAttribute("id", record.id);
      container.classList.add("step");
      if (idx === 0) {
        container.classList.add("active");
      }

      chapter.classList.add(config.theme);
      container.appendChild(chapter);
      container.classList.add(alignments[record.alignment]);
      features.appendChild(container);
    });

    story.appendChild(features);

    mapboxgl.accessToken = config.accessToken;

    const transformRequest = (url) => {
      const hasQuery = url.indexOf("?") !== -1;
      const suffix = hasQuery
        ? "&pluginName=journalismScrollytelling"
        : "?pluginName=journalismScrollytelling";
      return {
        url: url + suffix,
      };
    };

    var map = new mapboxgl.Map({
      container: "map",
      style: config.style,
      center: config.chapters[0].location.center,
      zoom: config.chapters[0].location.zoom,
      bearing: config.chapters[0].location.bearing,
      pitch: config.chapters[0].location.pitch,
      scrollZoom: false,
      transformRequest: transformRequest,
    });

    var scroller = scrollama();

    map.on("load", function () {
      map.addSource("places", {
        type: "geojson",
        data: {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                description:
                  "<strong>Chun Shui Tang（春水堂）</strong><img src='img/chunshuitang.png' class='popup-img'><p class='popup'>Address:No. 30. Siwei Street, West District, Taichung City, Taiwan 403 <br> Quote: Liu Han-Chieh: I was sitting in a staff meeting and had brought with me a typical Taiwanese dessert called fen yuan, a sweetened tapioca pudding. Just for fun I poured the tapioca balls into iced tea and drank it </p>",
              },
              geometry: {
                type: "Point",
                coordinates: [120.21333, 22.99083],
              },
            },
            {
              type: "Feature",
              properties: {
                description:
                  "<strong>Hanlin Tea Room(翰林茶馆）</strong><img src='img/hanlinteahouse.png' class='popup-img2'><p class='popup'>Address: 702, Taiwan, Tainan City, South District, Xinxiao Road, No.191 <br> Quote: Tu Tsung-ho: created the drink in 1986 after he spotted some white tapioca pearls at a market and decided they would go well with milk tea </p>",
              },
              geometry: {
                type: "Point",
                coordinates: [120.673648, 24.147736],
              },
            },
          ],
        },
      });

      map.addLayer({
        id: "places",
        type: "circle",
        source: "places",
        paint: {
          "circle-color": "#C57650",
          "circle-radius": 12,
          "circle-stroke-width": 6,
          "circle-stroke-color": "#FFF7EB",
        },
      });

      var popup = new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      });

      map.on("mouseenter", "places", function (e) {
        console.log("places!");
        // Change the cursor style as a UI indicator.
        map.getCanvas().style.cursor = "pointer";

        var coordinates = e.features[0].geometry.coordinates.slice();
        var description = e.features[0].properties.description;

        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
          coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        }

        // Populate the popup and set its coordinates
        // based on the feature found.
        popup.setLngLat(coordinates).setHTML(description).addTo(map);
      });

      map.on("mouseleave", "places", function () {
        map.getCanvas().style.cursor = "";
        popup.remove();
      });

      // setup the instance, pass callback functions
      scroller
        .setup({
          step: ".step",
          offset: 0.5,
          progress: true,
        })
        .onStepEnter((response) => {
          d3.select("#title").html("");
          var chapter = config.chapters.find(
            (chap) => chap.id === response.element.id
          );
          //var layer = document.getElementById("layer");
          response.element.classList.add("active");
          map[config.mapAnimation || "flyTo"](chapter.location);
          document.getElementById("map").style.opacity = 1;

          if (chapter.id == 0) {
            d3.select("#features").style("background-color", "#F9E8D0");
          }
        })
        .onStepExit((response) => {
          var chapter = config.chapters.find(
            (chap) => chap.id === response.element.id
          );
          response.element.classList.remove("active");
          if (chapter.onChapterExit.length > 0) {
            chapter.onChapterExit.forEach(setLayerOpacity);
          }
          document.getElementById("map").style.opacity = 0;
        });
    });

    window.addEventListener("resize", scroller.resize);

    for (let marker in yelp_markers) {
      var el = document.createElement("div");
      el.className = "marker";
      new mapboxgl.Marker(el)
        .setLngLat([yelp_markers[marker][0], yelp_markers[marker][1]])
        .setPopup(new mapboxgl.Popup().setHTML(marker))
        .addTo(map);
    }
  },
};
</script>

