import { createWebHistory, createRouter } from "vue-router";
import Intro from "@/components/Intro.vue";
import Home from "@/components/Menus.vue";
import About from "@/components/Colors2.vue";
import LDA from "@/components/LDA.vue";
import LDAprev from "@/components/LDA-prev.vue";
import Splash from "@/components/Splash.vue";
import Ending from "@/components/Ending.vue";

const routes = [{
        path: "/Bobavue/",
        name: "Splash",
        component: Splash,
    }, {
        path: "/Bobavue/Intro",
        name: "Intro",
        component: Intro,
    },
    {
        path: "/Bobavue/Menus",
        name: "Menus",
        component: Home,
    }, {
        path: "/Bobavue/Colors",
        name: "Colors",
        component: About,
    }, {
        path: "/Bobavue/LDA-1",
        name: "LDA-1",
        component: LDAprev,
    }, {
        path: "/Bobavue/LDA",
        name: "LDA",
        component: LDA,
    }, {
        path: "/Bobavue/End",
        name: "End",
        component: Ending,
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;