<template>
  <el-row>
    <div class="lda-header">
      <div class="lda-header-1">Reason 3: A "complicated" mark of Asian Identity</div>
      <div class="lda-header-2">
        people have a lot to say about boba when it comes to:
      </div>
      <div class="lda-header-2">
        <el-button class="coloremphasis but1" type="text" v-on:click="draw(1)"
          >Identity,</el-button
        >
        <el-button class="coloremphasis but2" type="text" v-on:click="draw(2)"
          >Culture,</el-button
        >
        <el-button class="coloremphasis but3" type="text" v-on:click="draw(3)"
          >Complicated Symbolism</el-button
        >
        and
        <el-button class="coloremphasis but4" type="text" v-on:click="draw(4)"
          >Boba Liberal</el-button
        >
      </div>
    </div>
  </el-row>
  <div id="chart"></div>
  <el-row>
    <el-col :span="3" :offset="1"
      ><div class="topic">
        <el-row
          ><el-col :span="2" :offset="18"
            ><h3 id="topic1">
              <p class="smallemph">Topic 1</p>
              Asian American Identity
            </h3></el-col
          ></el-row
        >
      </div></el-col
    >
    <el-col :span="4" :offset="1"
      ><div class="tweet" id="box1-2">
        <p class="tweet-header">American</p>
        There’s a culture that’s helped a lot of us develop an identity as being
        Asian <span class="one">American</span> in recent years, a lot of it is
        centered around food, boba, Asian parent jokes and shared childhood
        traumas, and you guessed it.... Kpop and Asians in entertainment
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box1-3">
        <p class="tweet-header">Generation</p>
        Boba <span class="one">generation</span> is our identity. Just a drink?
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box1-4">
        <p class="tweet-header">Stereotype</p>
        An Asian in the west who identifies as liberal and uses their political
        leaning as a veneer to pretend to be white or get closer to whiteness.
        Their Asian identity is very shallow as displayed by the
        <span class="one">stereotype</span> of "liking boba tea," a
        stereotypical Asian trait.
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box1-5">
        <p class="tweet-header">Meme</p>
        Bubble tea is a gimmick, a <span class="one">meme</span>, a stereotype,
        but it’s also a reference point for identity that generations of Asian
        Americans have used to cleave out their own place in the world... This
        is our lives. via @Eater @jennygzhang
      </div></el-col
    >
    <el-col :span="1" :offset="0.5"
      ><div>
        <el-button
          v-on:click="refresh(0)"
          icon="el-icon-refresh"
          class="refresh"
          circle
        ></el-button></div
    ></el-col>
  </el-row>
  <el-row>
    <el-col :span="3" :offset="1"
      ><div class="topic">
        <el-row
          ><el-col :span="2" :offset="18"
            ><h3 id="topic2">
              <p class="smallemph">Topic 2</p>
              Culture
            </h3></el-col
          ></el-row
        >
      </div></el-col
    >
    <el-col :span="4" :offset="1"
      ><div class="tweet" id="box2-2">
        <p class="tweet-header">People</p>
        What if we started acknowledging that Asian American as an identity
        includes more groups of <span class="two">people</span> than just those
        with a cultural connection to boba. What if we recognize that not all
        Asian Americans live in places where boba is even accessible.
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box2-3">
        <p class="tweet-header">Drink</p>
        Been <span class="two">drink</span>ing a lot of milk tea in part to feel
        a sense of normalcy and came across this article, which beautifully
        explains the pitfalls of conflating food w/ identity (but also its
        necessity in constructing a community and culture)
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box2-4">
        <p class="tweet-header">Food</p>
        Boba, pho, chinese <span class="two">food</span> - these cannot be
        culture b/c they cannot define identity. They become culture when shared
        with others over time to form a cultural consciousness - the culture is
        not the commodity but the awareness of a unique identity that links you
        to your bro/sisters.
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box2-5">
        <p class="tweet-header">Community</p>
        On Thursdays, we drink boba. Boba/bbt, whatever you call it, has become
        a symbol of Asian-American identity. Places like @bopomofocafe reflect
        how we use bbt as a symbol of
        <span class="two">community</span> gathering.
      </div></el-col
    >
    <el-col :span="1" :offset="0.5"
      ><div>
        <el-button
          v-on:click="refresh(1)"
          icon="el-icon-refresh"
          class="refresh"
          circle
        ></el-button></div
    ></el-col>
  </el-row>
  <el-row>
    <el-col :span="3" :offset="1"
      ><div class="topic">
        <el-row
          ><el-col :span="2" :offset="18">
            <h3 id="topic3">
              <p class="smallemph">Topic 3</p>
              Complicated Symbolism
            </h3></el-col
          ></el-row
        >
      </div></el-col
    >
    <el-col :span="4" :offset="1"
      ><div class="tweet" id="box3-2">
        <p class="tweet-header">Tea</p>
        literally if i’m ever mad at you just buy me a boba
        <span class="three">tea</span>...like immediately forgiven, slate clean,
        new identity, “hi, what’s your name, would you like a
        bagel?”$@%^&gt;£!!!
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box3-3">
        <p class="tweet-header">Symbol</p>
        boba has become one of the most prominent
        <span class="three">symbols</span> of identity for east and southeast
        asians in the western diaspora, and consuming &amp; liking it a mode of
        identity expression unique to western diaspora culture its good but
        tastes better bc it makes you feel asian lol
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box3-4">
        <p class="tweet-header">Asian Americans</p>
        However, it was an opening through which I felt I could explore my
        identity through a critical lens (ended up majoring in
        <span class="three">Asian American</span>
        Studies). Falling into “boba liberalism” is dangerous and v easy!
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box3-5">
        <p class="tweet-header">Complicated</p>
        How Bubble Tea Became a <span class="three">Complicated</span> Symbol of
        Asian-American Identity - Eater
      </div></el-col
    >
    <el-col :span="1" :offset="0.5"
      ><div>
        <el-button
          v-on:click="refresh(2)"
          icon="el-icon-refresh"
          class="refresh"
          circle
        ></el-button></div
    ></el-col>
  </el-row>
  <el-row>
    <el-col :span="3" :offset="1"
      ><div class="topic">
        <el-row
          ><el-col :span="2" :offset="18"
            ><h3 id="topic4">
              <p class="smallemph">Topic 4</p>
              Boba Liberal
            </h3></el-col
          ></el-row
        >
      </div></el-col
    >
    <el-col :span="4" :offset="1"
      ><div class="tweet" id="box4-2">
        <p class="tweet-header">Liberals</p>
        "a boba <span class="four">liberal</span> is someone who centers her
        Asian identity in buzzy cultural objects but lacks true engagement with
        the politics of her Asian identity. It’s the Asian who posts boba
        selfies as a way to prove her Asian bona fides while, seeking acceptance
        within white culture"
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box4-3">
        <p class="tweet-header">Political</p>
        Yay boba as a gateway to a more complex and real understanding of the
        Asian American experience, the limits of identity politics, and the
        importance of a <span class="four">political</span> ideology oriented
        towards liberation.
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box4-4">
        <p class="tweet-header">White</p>
        ‘boba liberal’ is like asian american version of
        <span class="four">white</span> feminism kinda.... privileged kids w
        shallow politics that mirror white liberal talking points and whose
        identity focuses on being an asian american w/o ever attempting to
        connect w their ethnicities
      </div></el-col
    >
    <el-col :span="4" :offset="0.5"
      ><div class="tweet" id="box4-5">
        <p class="tweet-header">Diaspora</p>
        Asian American Boba Liberals really need to read a history book in
        between their dumpling <span class="four">diaspora</span> identity
        articles #aapi #asianamerica
      </div></el-col
    >
    <el-col :span="1" :offset="0.5"
      ><div>
        <el-button
          v-on:click="refresh(3)"
          icon="el-icon-refresh"
          class="refresh"
          circle
        ></el-button></div
    ></el-col>
  </el-row>
  <el-button
    icon="el-icon-arrow-right"
    v-on:click="$router.push('/Bobavue/End')"
    class="navi-button"
  ></el-button>
  <el-button
    icon="el-icon-arrow-left"
    v-on:click="$router.push('/Bobavue/LDA-1')"
    class="navi-button-left"
  ></el-button>
</template>

<script>
import * as d3 from "d3";

let tweets, n1, n2, n3, n4;

const nodes = [
  { id: "Identity", group: 1, dim: [626, 430, 105] },
  { id: "american", group: 1, dim: [498, 656, 110] },
  { id: "generation", group: 1, dim: [872, 578, 105] },
  { id: "stereotype", group: 1, dim: [878, 372, 56] },
  { id: "meme", group: 1, dim: [677, 292, 50] },
  { id: "Culture", group: 2, dim: [1335, 307, 105] },
  { id: "people", group: 2, dim: [626, 430, 1] },
  { id: "drink", group: 2, dim: [626, 430, 1] },
  { id: "food", group: 2, dim: [626, 430, 1] },
  { id: "community", group: 2, dim: [626, 430, 1] },
  { id: "Complicated Symbolism", group: 3, dim: [1362, 571, 105] },
  { id: "tea", group: 3, dim: [626, 430, 1] },
  { id: "symbol", group: 3, dim: [626, 430, 1] },
  { id: "asian americans", group: 3, dim: [626, 430, 1] },
  { id: "complicated", group: 3, dim: [626, 430, 1] },
  { id: "Boba Liberal", group: 4, dim: [1030, 811, 105] },
  { id: "liberals", group: 4, dim: [626, 430, 1] },
  { id: "political", group: 4, dim: [626, 430, 1] },
  { id: "white", group: 4, dim: [626, 430, 1] },
  { id: "diaspora", group: 4, dim: [626, 430, 1] },
];

const links = [
  { source: "american", target: "Identity", value: 5 },
  { source: "generation", target: "Identity", value: 5 },
  { source: "stereotype", target: "Identity", value: 5 },
  { source: "meme", target: "Identity", value: 6 },
  { source: "people", target: "Culture", value: 1 },
  { source: "drink", target: "Culture", value: 1 },
  { source: "food", target: "Culture", value: 1 },
  { source: "community", target: "Culture", value: 1 },
  { source: "tea", target: "Complicated Symbolism", value: 2 },
  { source: "symbol", target: "Complicated Symbolism", value: 1 },
  { source: "asian americans", target: "Complicated Symbolism", value: 1 },
  { source: "complicated", target: "Complicated Symbolism", value: 3 },
  { source: "liberals", target: "Boba Liberal", value: 3 },
  { source: "political", target: "Boba Liberal", value: 5 },
  { source: "white", target: "Boba Liberal", value: 1 },
  { source: "diaspora", target: "Boba Liberal", value: 1 },
];

let topic1 = {
  american: [0.076, []],
  generation: [0.022, []],
  stereotype: [0.021, []],
  meme: [0.021, []],
};
let topic2 = {
  people: [0.034, []],
  drink: [0.024, []],
  food: [0.023, []],
  community: [0.021, []],
};
let topic3 = {
  tea: [0.15, []],
  symbol: [0.13, []],
  "asian americans": [0.13, []],
  complicated: [0.12, []],
};
let topic4 = {
  liberals: [0.051, []],
  political: [0.048, []],
  white: [0.024, []],
  diaspora: [0.022, []],
};

function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min);
}

export default {
  name: "boba time",
  data() {
    return {
      currentTopic: topic1,
      currentColor: "one",
      currentHeader: "Identity",
      left: "disabled",
      right: "topic2",
    };
  },
  mounted() {
    d3.select("#title").html(
      "<img src='img/reason_3.png' style='height: 60px; width: 280px;'>"
    );

    d3.csv("boba-identity.csv", function (d) {
      return {
        tweet: d.text,
      };
    }).then((data) => {
      tweets = data;

      data.forEach(function (d, i) {
        for (let topic in topic1) {
          if (d.tweet.toLowerCase().includes(topic)) {
            topic1[topic][1].push(i);
          }
        }
        for (let topic in topic2) {
          if (d.tweet.toLowerCase().includes(topic)) {
            topic2[topic][1].push(i);
          }
        }
        for (let topic in topic3) {
          if (d.tweet.toLowerCase().includes(topic)) {
            topic3[topic][1].push(i);
          }
        }
        for (let topic in topic4) {
          if (d.tweet.toLowerCase().includes(topic)) {
            topic4[topic][1].push(i);
          }
        }
      });

      console.log(topic1, topic2, topic3, topic4);

      //this.collapseforce();
    });
  },
  methods: {
    collapseforce: function () {
      const data = {
        name: "all",
        size: 50,
        children: [
          {
            name: "Identity",
            size: 50,
            x: 626,
            y: 430,
            children: [
              { name: "american", size: 110, x: 498, y: 656 },
              { name: "generation", size: 105, x: 872, y: 578 },
              { name: "stereotype", size: 56, x: 878, y: 372 },
              { name: "meme", size: 50, x: 677, y: 292 },
            ],
          },
          {
            name: "Culture",
            size: 50,
            children: [
              { name: "people", size: 50, x: 498, y: 656 },
              { name: "drink", size: 50, x: 498, y: 656 },
              { name: "food", size: 50, x: 498, y: 656 },
              { name: "community", size: 50, x: 498, y: 656 },
            ],
          },
          {
            name: "Complicated Symbolism",
            size: 50,
            children: [
              { name: "tea", size: 50, x: 498, y: 656 },
              { name: "symbol", size: 50, x: 498, y: 656 },
              { name: "asian americans", size: 50, x: 498, y: 656 },
              { name: "complicated", size: 50, x: 498, y: 656 },
            ],
          },
          {
            name: "Boba Liberal",
            size: 50,
            children: [
              { name: "liberals", size: 50, x: 498, y: 656 },
              { name: "political", size: 50, x: 498, y: 656 },
              { name: "white", size: 50, x: 498, y: 656 },
              { name: "diaspora", size: 50, x: 498, y: 656 },
            ],
          },
        ],
      };

      const width = 1800,
        height = 1000;

      let i = 0;

      const root = d3.hierarchy(data);
      let node, link;

      const svg = d3
        .select("#chart")
        .append("svg")
        .attr("id", "ldasvg")
        .attr("width", width)
        .attr("height", height);

      const simulation = d3
        .forceSimulation()
        .force(
          "link",
          d3.forceLink().id(function (d) {
            return d.id;
          })
        )
        .force("charge", d3.forceManyBody().strength(-15).distanceMax(300))
        .force("center", d3.forceCenter(width / 2, height / 4))
        .on("tick", ticked);

      function update() {
        const nodes = flatten(root);
        const links = root.links();

        link = svg.selectAll(".link").data(links, function (d) {
          return d.target.id;
        });

        link.exit().remove();

        const linkEnter = link
          .enter()
          .append("line")
          .attr("class", "link")
          .style("stroke", "#000")
          .style("opacity", "0.2")
          .style("stroke-width", 2);

        link = linkEnter.merge(link);

        node = svg.selectAll(".node").data(nodes, function (d) {
          return d.id;
        });

        node.exit().remove();

        const nodeEnter = node
          .enter()
          .append("g")
          .attr("class", "node")
          .attr("stroke", "#666")
          .attr("stroke-width", 2)
          .attr("x", (d) => d.x)
          .attr("y", (d) => d.y)
          .style("fill", color)
          .style("opacity", 1)
          .on("click", function (e, d) {
            if (!e.defaultPrevented) {
              if (d.children) {
                d._children = d.children;
                d.children = null;
              } else {
                d.children = d._children;
                d._children = null;
              }
              update();
            }
          });

        nodeEnter
          .append("circle")
          .attr("r", function (d) {
            return d.data.size / 10;
          })
          .attr("cx", (d) => d.data.x)
          .attr("cy", (d) => d.data.y)
          .style("text-anchor", function (d) {
            return d.children ? "end" : "start";
          })
          .text(function (d) {
            return d.data.name;
          });

        node = nodeEnter.merge(node);
        simulation.nodes(nodes);
        simulation.force("link").links(links);
      }

      function color(d) {
        return d._children
          ? "#51A1DC" // collapsed package
          : d.children
          ? "#51A1DC" // expanded package
          : "#F94B4C"; // leaf node
      }

      function ticked() {
        link
          .attr("x1", function (d) {
            return d.source.x;
          })
          .attr("y1", function (d) {
            return d.source.y;
          })
          .attr("x2", function (d) {
            return d.target.x;
          })
          .attr("y2", function (d) {
            return d.target.y;
          });

        node.attr("cx", (d) => d.x).attr("cy", (d) => d.y);
        /*         node.attr("transform", function (d) {
          console.log(node)
          return `translate(${d.x}, ${d.y})`;
        }); */
      }

      function flatten(root) {
        const nodes = [];
        function recurse(node) {
          if (node.children) node.children.forEach(recurse);
          if (!node.id) node.id = ++i;
          else ++i;
          nodes.push(node);
        }
        recurse(root);
        return nodes;
      }

      update();
    },
    drawForce: function () {
      console.log(nodes, links);
      let width = 1800,
        height = 800;

      const simulation = d3
        .forceSimulation(nodes)
        .force(
          "link",
          d3
            .forceLink(links)
            .id((d) => d.id)
            .distance(100)
        )
        .force("charge", d3.forceManyBody())
        .force(
          "forceX",
          d3.forceX((d) => d.dim[0] + d.dim[2])
        )
        .force(
          "forceY",
          d3.forceY((d) => d.dim[1] + d.dim[2] - 250)
        )
        .force("collide", d3.forceCollide(50));

      const svg = d3
        .select("#chart")
        .append("svg")
        .attr("width", width)
        .attr("height", height);

      const link = svg
        .append("g")
        .attr("stroke", "#999")
        .attr("stroke-opacity", 0.6)
        .selectAll("line")
        .data(links)
        .join("line")
        .attr("stroke-width", (d) => Math.sqrt(d.value));

      const node = svg
        .append("g")
        .attr("stroke", "#fff")
        .attr("stroke-width", 1.5)
        .selectAll("circle")
        .data(nodes)
        .join("circle")
        .attr("r", (d) => d.dim[2])
        .attr("fill", "black");

      node.append("title").text((d) => d.id);

      simulation.on("tick", () => {
        link
          .attr("x1", (d) => d.source.dim[0] + d.source.dim[2])
          .attr("y1", (d) => d.source.dim[1] + d.source.dim[2] - 250)
          .attr("x2", (d) => d.target.dim[0] + d.target.dim[2])
          .attr("y2", (d) => d.target.dim[1] + d.target.dim[2] - 250);

        node
          .attr("cx", (d) => d.dim[0] + d.dim[2])
          .attr("cy", (d) => d.dim[1] + d.dim[2] - 250);
      });

      //invalidation.then(() => simulation.stop());

      return svg.node();
    },
    draw: function (t) {
      let topic = topic1;
      let colorclass;
      switch (t) {
        case 1:
          topic = topic1;
          colorclass = "one";
          break;
        case 2:
          topic = topic2;
          colorclass = "two";
          break;
        case 3:
          topic = topic3;
          colorclass = "three";
          break;
        case 4:
          topic = topic4;
          colorclass = "four";
          break;
      }
      this.currentTopic = topic;
      this.currentColor = colorclass;

      d3.selectAll(".topic").attr("class", "topic " + colorclass);
      d3.selectAll("#topic1").attr("class", colorclass);
      d3.selectAll("#topic2").attr("class", colorclass);
      d3.selectAll("#topic3").attr("class", colorclass);
      d3.selectAll("#topic4").attr("class", colorclass);

      Object.keys(topic).forEach(function (key, i) {
        d3.select("#topic" + (i + 1)).text(key);
        let regEx = new RegExp(key, "ig");

        n1 = getRandomInt(0, topic[key][1].length - 1);
        d3.select("#box" + (i + 1) + "-2").html(
          tweets[topic[key][1][n1]].tweet.replaceAll(
            regEx,
            '<span class="' + colorclass + '">' + key + "</span>"
          )
        );
        n2 = getRandomInt(0, topic[key][1].length);
        while (n1 == n2) {
          n2 = getRandomInt(0, topic[key][1].length);
        }
        d3.select("#box" + (i + 1) + "-3").html(
          tweets[topic[key][1][n2]].tweet.replaceAll(
            regEx,
            '<span class="' + colorclass + '">' + key + "</span>"
          )
        );
        n3 = getRandomInt(0, topic[key][1].length);
        while ((n1 == n3) | (n2 == n3)) {
          n3 = getRandomInt(0, topic[key][1].length);
        }
        d3.select("#box" + (i + 1) + "-4").html(
          tweets[topic[key][1][n3]].tweet.replaceAll(
            regEx,
            '<span class="' + colorclass + '">' + key + "</span>"
          )
        );
        n4 = getRandomInt(0, topic[key][1].length);
        while ((n1 == n4) | (n2 == n4) | (n3 == n4)) {
          n4 = getRandomInt(0, topic[key][1].length);
        }
        d3.select("#box" + (i + 1) + "-5").html(
          tweets[topic[key][1][n4]].tweet.replaceAll(
            regEx,
            '<span class="' + colorclass + '">' + key + "</span>"
          )
        );
      });
    },
    refresh(n) {
      let topic = this.currentTopic;
      let key = Object.keys(topic)[n];
      let colorclass = this.currentColor;
      let regEx = new RegExp(key, "ig");

      console.log(colorclass);

      d3.select("#topic" + (n + 1)).text(key);

      n1 = getRandomInt(0, topic[key][1].length);
      d3.select("#box" + (n + 1) + "-2").html(
        tweets[topic[key][1][n1]].tweet.replaceAll(
          regEx,
          '<span class="' + colorclass + '">' + key + "</span>"
        )
      );
      n2 = getRandomInt(0, topic[key][1].length);
      while (n1 == n2) {
        n2 = getRandomInt(0, topic[key][1].length);
      }
      d3.select("#box" + (n + 1) + "-3").html(
        tweets[topic[key][1][n2]].tweet.replaceAll(
          regEx,
          '<span class="' + colorclass + '">' + key + "</span>"
        )
      );
      n3 = getRandomInt(0, topic[key][1].length);
      while ((n1 == n3) | (n2 == n3)) {
        n3 = getRandomInt(0, topic[key][1].length);
      }
      d3.select("#box" + (n + 1) + "-4").html(
        tweets[topic[key][1][n3]].tweet.replaceAll(
          regEx,
          '<span class="' + colorclass + '">' + key + "</span>"
        )
      );
      n4 = getRandomInt(0, topic[key][1].length);
      while ((n1 == n4) | (n2 == n4) | (n3 == n4)) {
        n4 = getRandomInt(0, topic[key][1].length);
      }
      d3.select("#box" + (n + 1) + "-5").html(
        tweets[topic[key][1][n4]].tweet.replaceAll(
          regEx,
          '<span class="' + colorclass + '">' + key + "</span>"
        )
      );
    },
    leftButton: function () {
      switch (this.left) {
        case "disabled":
          console.log("disabled button");
          break;
        case "topic1":
          this.draw(topic1);
          this.currentHeader = "Identity";
          this.left = "disabled";
          this.right = "topic2";
          break;
        case "topic2":
          this.draw(topic2);
          this.currentHeader = "Culture";
          this.left = "topic1";
          this.right = "topic3";
          break;
        case "topic3":
          this.draw(topic3);
          this.currentHeader = "Complicated Symbolism";
          this.left = "topic2";
          this.right = "topic4";
          break;
        case "topic4":
          this.draw(topic4);
          this.currentHeader = "Boba Liberal";
          this.left = "topic3";
          this.right = "disabled";
          break;
      }
    },
    rightButton: function () {
      switch (this.right) {
        case "disabled":
          console.log("disabled button");
          break;
        case "topic1":
          this.draw(topic1);
          this.currentHeader = "Identity";
          this.left = "disabled";
          this.right = "topic2";
          break;
        case "topic2":
          this.draw(topic2);
          this.currentHeader = "Culture";
          this.left = "topic1";
          this.right = "topic3";
          break;
        case "topic3":
          this.draw(topic3);
          this.currentHeader = "Complicated Symbolism";
          this.left = "topic2";
          this.right = "topic4";
          break;
        case "topic4":
          this.draw(topic4);
          this.currentHeader = "Boba Liberal";
          this.left = "topic3";
          this.right = "disabled";
          break;
      }
    },
  },
};
</script>

