<template>
  <div style="width: 95vw; overflow: hidden">
    <el-col :span="5" :offset="1" style="margin-top: 450px; position: fixed">
      <p class="treewords">
        <span class="bigemphasis">1,350</span> options from a random sample of
        30 boba shops in NYC
      </p>
      <p class="treewords">
        <ul class="triple">
          <li>Alimama</li>
          <li>BIAO Sugar</li>
          <li>Boba Fries</li>
          <li>Bubbly Tea</li>
          <li>Chun Yang Tea Chinatown</li>
          <li>Coco Fresh Tea and Juice</li>
          <li>Debutea</li>
          <li>Dragon Tea</li>
          <li>FIFTYLAN Washington Square Park</li>
          <li>Gong Cha</li>
          <li>Kung Fu Tea</li>
          <li>M Tea</li>
          <li>Machi Machi</li>
          <li>Mi Tea</li>
          <li>Moge Tee</li>
          <li>Mr. Buncha</li>
          <li>Mr. Wish</li>
          <li>OJBK Tea Shop</li>
          <li>PaTea</li>
          <li>Shiny Tea</li>
          <li>Song Tea</li>
          <li>Tea and Milk</li>
          <li>Tea Magic</li>
          <li>Teado Tea Shop</li>
          <li>Teazzi Tea Shop</li>
          <li>Ten Ren's Tea Time</li>
          <li>The Alley</li>
          <li>Tiger Sugar</li>
          <li>Truedan</li>
          <li>Trutea</li>
          <li>ViVi Bubble Tea</li>
          <li>Xing Fu Tang</li>
          <li>YanYan Tea</li>
          <li>Yi Fang TaiwanFruit Tea</li>
        </ul>
      </p>
    </el-col>
    <el-col :span="14" :offset="9">
      <el-row
        ><span class="menu-header">{{ currentHeader }}</span></el-row
      ><el-row>
        <span class="menu-subheader"
          >boba is served in a variety of
          <span class="coloremphasis">
            <el-button class="coloremphasis" type="text" v-on:click="drawBase()"
              >liquid bases,</el-button
            ></span
          >
          countless
          <span class="coloremphasis">
            <el-button
              class="coloremphasis"
              type="text"
              v-on:click="drawFlavors()"
              >flavors</el-button
            ></span
          >
          and lots of
          <span class="coloremphasis">
            <el-button
              class="coloremphasis"
              type="text"
              v-on:click="drawToppings()"
              >toppings</el-button
            ></span
          ></span
        ></el-row
      >
    </el-col>
    <div id="vis"></div>
    <div id="barchart"></div>
    <el-button
      icon="el-icon-arrow-right"
      v-on:click="$router.push('/Bobavue/Colors')"
      class="navi-button"
    ></el-button>
        <el-button
      icon="el-icon-arrow-left"
      v-on:click="$router.push('/Bobavue/Intro')"
      class="navi-button-left"
    ></el-button>
  </div>
</template>

<script>
import * as d3 from "d3";

let svg, chartsvg, div;
let simulation, nodes;
let dataset;
let base_colorScale;
let bases = {
    tea: [],
    milk: [],
    slush: [],
    drink: [],
    juice: [],
    yogurt: [],
    other: [],
    smoothie: [],
    coffee: [],
  },
  flavors = {
    fruit: [],
    milk: [],
    undefined: [],
    "brown sugar": [],
    honey: [],
    other: [],
    matcha: [],
    taro: [],
    flower: [],
    chocolate: [],
    coffee: [],
    caramel: [],
  },
  toppings = {
    unspecified: [],
    "tapioca ball": [],
    other: [],
    jelly: [],
    cream: [],
    cheese: [],
    "red bean": [],
    taro: [],
    "milk foam": [],
  };

const base = [
  "tea",
  "slush",
  "coffee",
  "drink",
  "juice",
  "milk",
  "other",
  "shredded ice",
  "chocolate",
  "smoothie",
  "yogurt",
];

const baseXY = {
  tea: [350, 300, 350],
  slush: [150, 0, 225],
  coffee: [450, 0, 150],
  drink: [-100, 300, 225],
  juice: [-50, 100, 200],
  milk: [800, 300, 250],
  yogurt: [50, 400, 175],
  smoothie: [600, 450, 175],
  other: [600, 0, 175],
};

const flavorXY = {
  "brown sugar": [-100, 75, 225],
  caramel: [150, 0, 150],
  chocolate: [350, -50, 165],
  coffee: [600, 0, 160],
  flower: [-100, 475, 175],
  fruit: [350, 250, 325],
  honey: [650, 250, 225],
  matcha: [750, 100, 185],
  milk: [25, 325, 250],
  taro: [200, 500, 170],
  other: [500, 500, 175],
  undefined: [850, 375, 250],
};

const topXY = {
  cheese: [0, 0, 200],
  cream: [250, -25, 185],
  jelly: [550, 25, 190],
  "milk foam": [100, 500, 150],
  "red bean": [750, 100, 175],
  "tapioca ball": [-50, 300, 225],
  taro: [700, 300, 160],
  other: [700, 500, 200],
  unspecified: [350, 300, 375],
};

const base_colors = [
  "#855647",
  "#EA9D49",
  "#C57650",
  "#4F7A70",
  "#EC9FFF",
  "#F56E55",
  "#8555D9",
  "#EC9FFF",
  "#784A44",
  "#3132B1",
  "#6D4F76",
  "#39556D",
];

export default {
  data() {
    return {
      currentHeader: "Reason 1: Order like a Pro",
      left: "disabled",
      right: "format",
      formats: 0,
      flavors: 0,
      toppings: 0,
    };
  },
  components: {},
  mounted() {
    d3.csv("tea.csv", function (d) {
      return {
        shop: d.shop,
        item: d.menu,
        format: d.format,
        base: d.base,
        milk: d.milk,
        liquid: d.liquid,
        flavor: d.flavor,
        toppings: d.toppings,
        tops: d.tops,
      };
    }).then((data) => {
      dataset = [];

      div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip")
        .style("opacity", 0);

      data.forEach((d) => {
        d.liquid.split(",").forEach((l) => {
          d.toppings.split(",").forEach((t) => {
            dataset.push({
              shop: d.shop,
              item: d.item,
              format: d.format,
              base: d.base,
              milk: d.milk,
              liquid: l,
              flavor: d.flavor,
              toppings: t,
              tops: d.tops,
            });
          });
        });
      });

      d3.select("#title").html(
        "<img src='img/reason_1.png' style='height: 60px; width: 280px;'>"
      );

      let formatcount = {};
      for (let i = 0; i < dataset.length; i++) {
        formatcount[dataset[i].format] =
          1 + (formatcount[dataset[i].format] || 0);
      }

      this.formats = Object.keys(formatcount).length;

      let flavorcount = {};
      for (let i = 0; i < dataset.length; i++) {
        flavorcount[dataset[i].liquid] =
          1 + (flavorcount[dataset[i].liquid] || 0);
      }

      this.flavors = Object.keys(flavorcount).length;

      let toppingcount = {};
      for (let i = 0; i < dataset.length; i++) {
        toppingcount[dataset[i].tops] =
          1 + (toppingcount[dataset[i].tops] || 0);
      }

      this.toppings = Object.keys(toppingcount).length;

      dataset.forEach(function (d, i) {
        if (bases[d.base]) {
          bases[d.base].push(i);
        } else {
          bases[d.base] = [];
        }
        if (flavors[d.flavor]) {
          flavors[d.flavor].push(i);
        } else {
          flavors[d.flavor] = [];
        }
        if (toppings[d.tops]) {
          toppings[d.tops].push(i);
        } else {
          toppings[d.tops] = [];
        }
      });

      //drawInitial
      svg = d3
        .select("#vis")
        .append("svg")
        .attr("width", 1800)
        .attr("height", 800)
        .attr("opacity", 1);

      chartsvg = d3
        .select("#barchart")
        .append("svg")
        .attr("id", "chartsvg")
        .attr("height", 800)
        .attr("width", 325);

      console.log(chartsvg);

      simulation = d3.forceSimulation(dataset);
      simulation.on("tick", () => {
        nodes.attr("cx", (d) => d.x).attr("cy", (d) => d.y);
      });

      simulation.stop();

      base_colorScale = d3.scaleOrdinal(base, base_colors);

      nodes = svg
        .selectAll("circle")
        .data(dataset)
        .enter()
        .append("circle")
        .attr("r", 6)
        .attr("cx", 5000)
        .attr("cy", 5000)
        .attr("opacity", 0.8)
        .on("mousemove", function (e, d) {
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html("Shop: " + d.shop + "<br>" + "Item: " + d.item)
            .style("left", e.pageX + "px")
            .style("top", e.pageY - 20 + "px");
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        })
        .attr("class", (d) => d.base)
        .attr("fill", (d) => base_colorScale(d.base));

      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5);

      this.drawAll();

      svg
        .selectAll(".base-text")
        .data(Object.keys(baseXY))
        .enter()
        .append("text")
        .attr("class", "base-text")
        .text((d) => d)
        .attr("x", (d) => baseXY[d][0] + 200)
        .attr("y", (d) => baseXY[d][1] + baseXY[d][2])
        .attr("opacity", 0)
        .attr("text-anchor", "middle")
        .attr("font-family", "Montserrat")
        .attr("font-weight", "bold")
        .attr("fill", "#e2dac8")
        .raise();

      svg
        .selectAll(".flavor-text")
        .data(Object.keys(flavorXY))
        .enter()
        .append("text")
        .attr("class", "flavor-text")
        .text((d) => d)
        .attr("x", (d) => flavorXY[d][0] + 200)
        .attr("y", (d) => flavorXY[d][1] + flavorXY[d][2])
        .attr("opacity", 0)
        .attr("text-anchor", "middle")
        .attr("font-family", "Montserrat")
        .attr("font-weight", "bold")
        .attr("fill", "#e2dac8")
        .raise();

      svg
        .selectAll(".topping-text")
        .data(Object.keys(topXY))
        .enter()
        .append("text")
        .attr("class", "topping-text")
        .text((d) => d)
        .attr("x", (d) => topXY[d][0] + 200)
        .attr("y", (d) => topXY[d][1] + topXY[d][2])
        .attr("opacity", 0)
        .attr("text-anchor", "middle")
        .attr("font-family", "Montserrat")
        .attr("font-weight", "bold")
        .attr("fill", "#e2dac8")
        .raise();
    });
  },
  methods: {
    drawAll() {
      svg.selectAll(".base-text").attr("opacity", 0);
      svg.selectAll(".flavor-text").attr("opacity", 0);
      svg.selectAll(".topping-text").attr("opacity", 0);

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force("forceX", d3.forceX(600))
        .force("forceY", d3.forceY(350))
        .force("collide", d3.forceCollide(7))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.6).restart();
    },
    drawBase() {
      svg.selectAll(".base-text").attr("opacity", 1);
      svg.selectAll(".flavor-text").attr("opacity", 0);
      svg.selectAll(".topping-text").attr("opacity", 0);

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force(
          "forceX",
          d3.forceX((d) => baseXY[d.base][0] + 200)
        )
        .force(
          "forceY",
          d3.forceY((d) => baseXY[d.base][1] + 100)
        )
        .force("collide", d3.forceCollide(7))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.6).restart();

      let px = 50;
      let py = 90;

      d3.selectAll(".treewords").style("opacity", 0);

      chartsvg.selectAll("*").remove();

      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 25)
        .text("Choose the kind of")
        .attr("id", "headerbar");
      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 50)
        .text("drink you prefer")
        .attr("id", "headerbar");

      let total = 0;
      Object.keys(bases).forEach(function (key) {
        total += bases[key].length;
      });

      Object.keys(bases).forEach(function (key, i) {
        chartsvg
          .append("text")
          .attr("font-family", "Montserrat")
          .attr("font-size", "14px")
          .attr("x", px)
          .attr("y", py)
          .text(key);

        chartsvg
          .append("rect")
          .attr("x", px)
          .attr("y", py + 10)
          .attr("height", 15)
          .attr("width", () => 200 * (bases[key].length / total))
          .attr("fill", base_colors[i]);

        chartsvg
          .append("text")
          .attr("font-family", "Montserrat")
          .attr("font-size", "14px")
          .attr("x", px + 210)
          .attr("y", py + 21)
          .text(Math.round((1000 * bases[key].length) / total) / 10 + "%");

        py += 50;
      });
    },
    drawFlavors() {
      svg.selectAll(".base-text").attr("opacity", 0);
      svg.selectAll(".flavor-text").attr("opacity", 1);
      svg.selectAll(".topping-text").attr("opacity", 0);

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force(
          "forceX",
          d3.forceX((d) => flavorXY[d.flavor][0] + 200)
        )
        .force(
          "forceY",
          d3.forceY((d) => flavorXY[d.flavor][1] + 100)
        )
        .force("collide", d3.forceCollide(7))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.4).restart();

      let px = 50;
      let py = 90;

      d3.selectAll(".treewords").style("opacity", 0);

      chartsvg.selectAll("*").remove();

      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 25)
        .text("Now lets choose one of")
        .attr("id", "headerbar");
      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 50)
        .text("the popular flavors")
        .attr("id", "headerbar");

      let total = 0;
      Object.keys(flavors).forEach(function (key) {
        total += flavors[key].length;
      });

      Object.keys(flavors).forEach(function (key) {
        chartsvg
          .append("text")
          .attr("font-family", "Montserrat")
          .attr("font-size", "14px")
          .attr("x", px)
          .attr("y", py)
          .text(key);

        chartsvg
          .append("rect")
          .attr("x", px)
          .attr("y", py + 10)
          .attr("height", 15)
          .attr("width", () => 200 * (flavors[key].length / total))
          .attr("fill", "#e2dac8");

        chartsvg
          .append("text")
          .attr("font-family", "Montserrat")
          .attr("font-size", "14px")
          .attr("x", px + 210)
          .attr("y", py + 21)
          .text(Math.round((1000 * flavors[key].length) / total) / 10 + "%");

        py += 50;
      });
    },
    drawToppings() {
      svg.selectAll(".base-text").attr("opacity", 0);
      svg.selectAll(".flavor-text").attr("opacity", 0);
      svg.selectAll(".topping-text").attr("opacity", 1);

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force(
          "forceX",
          d3.forceX((d) => topXY[d.tops][0] + 200)
        )
        .force(
          "forceY",
          d3.forceY((d) => topXY[d.tops][1] + 100)
        )
        .force("collide", d3.forceCollide(7))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.6).restart();

      let px = 50;
      let py = 90;

      d3.selectAll(".treewords").style("opacity", 0);

      chartsvg.selectAll("*").remove();

      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 25)
        .text("Don't get overwhelmed")
        .attr("id", "headerbar");
      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 50)
        .text("by toppings")
        .attr("id", "headerbar");

      let total = 0;
      Object.keys(toppings).forEach(function (key) {
        total += toppings[key].length;
      });

      Object.keys(toppings).forEach(function (key) {
        chartsvg
          .append("text")
          .attr("font-family", "Montserrat")
          .attr("font-size", "14px")
          .attr("x", px)
          .attr("y", py)
          .text(key);

        chartsvg
          .append("rect")
          .attr("x", px)
          .attr("y", py + 10)
          .attr("height", 15)
          .attr("width", () => 200 * (toppings[key].length / total))
          .attr("fill", "#e2dac8");

        chartsvg
          .append("text")
          .attr("font-family", "Montserrat")
          .attr("font-size", "14px")
          .attr("x", px + 210)
          .attr("y", py + 21)
          .text(Math.round((1000 * toppings[key].length) / total) / 10 + "%");

        py += 50;
      });
    },
  },
};
</script>

<style>
.menu-header {
  width: 100%;
  margin-top: 80px;
  margin-bottom: 50px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: right;
}

.menu-subheader {
  width: 100%;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  margin-top: -40px;
  margin-bottom: 30px;
}

.triple {
  font-size: 10px;
  padding: 5px;
  columns: 3;
  -webkit-columns: 3;
  -moz-columns: 3;
}
#vis {
  position: fixed;
  margin-top: 200px;
  margin-left: 500px;
}
#barchart {
  position: fixed;
  top: 200px;
  left: 25px;
  height: 800px;
  width: 300px;
}

#headerbar {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 29px;
}

#chartsvg {
  margin-top: -75px;
}
</style>
