<template>
  <button v-on:click="update(1)" class='selection' id="selection-1"></button>
  <button v-on:click="update(2)" class='selection' id="selection-2"></button>
  <button v-on:click="update(3)" class='selection' id="selection-3"></button>
  <button v-on:click="update(4)" class='selection' id="selection-4"></button>
  <button v-on:click="update(5)" class='selection' id="selection-5"></button>
  <div id="graphic">
    <svg id="lifecycle" height="950" width="1500">
      <ellipse
        cx="625"
        cy="425"
        rx="300"
        ry="300"
        stroke="#E2DAC8"
        stroke-width="3"
        fill="none"
        opacity="0"
      ></ellipse>
      <image
        x="475"
        y="275"
        height="300"
        width="300"
        id="middlepic"
        href="img/boba_2.png"
      ></image>
      <image
        x="600"
        y="100"
        height="50"
        width="50"
        id="pic1"
        href="img/circle-15.svg"
        v-on:click="update(1)"
      ></image>
      <text x="617" y="133" class="builder-label" id="builder1" fill="#fff7eb">
        1
      </text>
      <rect
        x="700"
        y="115"
        height="65"
        width="200"
        class="label1 builder-rect"
        fill="#FFF7EB"
      ></rect>
      <text x="710" y="115" class="label1 builder-text-large" fill="#000000">
        1: Black tea as liquid base
      </text>
      <text x="710" y="135" class="label1 builder-text" fill="#000000">
        The Chinese have a history of drinking tea for thousands
      </text>
      <text x="710" y="150" class="label1 builder-text" fill="#000000">
        of years and have deeply influenced other East Asian
      </text>
      <text x="710" y="165" class="label1 builder-text" fill="#000000">
        counties and areas
      </text>
      <image
        x="330"
        y="270"
        height="50"
        width="50"
        id="pic2"
        href="img/circle-15.svg"
        v-on:click="update(2)"
      ></image>
      <text x="347" y="303" class="builder-label" id="builder2" fill="#fff7eb">
        2
      </text>
      <text x="0" y="280" class="label2 builder-text-large" fill="#000000">
        2: Milk/Powder milk
      </text>
      <text x="0" y="300" class="label2 builder-text" fill="#000000">
        as early as the Tang dynasty (618 - 917)
      </text>
      <text x="0" y="315" class="label2 builder-text" fill="#000000">
        the Chinese began adding butter,
      </text>
      <text x="0" y="330" class="label2 builder-text" fill="#000000">
        cream and milk to their tea.
      </text>
      <image
        x="380"
        y="600"
        height="50"
        width="50"
        id="pic3"
        href="img/circle-15.svg"
        v-on:click="update(3)"
      ></image>
      <text x="397" y="633" class="builder-label" id="builder3" fill="#fff7eb">
        3
      </text>
      <text x="0" y="610" class="label3 builder-text-large" fill="#000000">
        3: Tapioca balls
      </text>
      <text x="0" y="630" class="label3 builder-text" fill="#000000">
        South American cassava, cooked in boiling
      </text>
      <text x="0" y="645" class="label3 builder-text" fill="#000000">
        syrup and simmered slowly over low heat.
      </text>
      <text x="0" y="660" class="label3 builder-text" fill="#000000">
        popular in Taiwan named fenyuan 粉圆
      </text>
      <image
        x="820"
        y="600"
        height="50"
        width="50"
        id="pic4"
        href="img/circle-15.svg"
        v-on:click="update(4)"
      ></image>
      <text x="837" y="633" class="builder-label" id="builder4" fill="#fff7eb">
        4
      </text>
      <text x="930" y="615" class="label4 builder-text-large" fill="#000000">
        4: More sweetness
      </text>
      <text x="930" y="635" class="label4 builder-text" fill="#000000">
        people keep adding sugar into boba
      </text>
      <text x="930" y="650" class="label4 builder-text" fill="#000000">
        to satisfy the local craving for sugar.
      </text>
      <image
        x="870"
        y="270"
        height="50"
        width="50"
        id="pic5"
        href="img/circle-15.svg"
        v-on:click="update()"
      ></image>
      <text x="887" y="303" class="builder-label" id="builder5" fill="#fff7eb">
        5
      </text>
      <text x="980" y="285" class="label5 builder-text-large" fill="#000000">
        5: Wide straw
      </text>
      <text x="980" y="305" class="label5 builder-text" fill="#000000">
        wider than typical drinking straw because
      </text>
      <text x="980" y="320" class="label5 builder-text" fill="#000000">
        tapioca pearls need a bigger opening
      </text>
    </svg>
  </div>
</template>

<script>
import * as d3 from "d3";
export default {
  mounted() {
    this.update(1);
    d3.selectAll(".label1").attr("opacity", 0);
    d3.selectAll(".label2").attr("opacity", 0);
    d3.selectAll(".label3").attr("opacity", 0);
    d3.selectAll(".label4").attr("opacity", 0);
    d3.selectAll(".label5").attr("opacity", 0);
    d3.selectAll("#pic1").attr("opacity", 0);
    d3.selectAll("#pic2").attr("opacity", 0);
    d3.selectAll("#pic3").attr("opacity", 0);
    d3.selectAll("#pic4").attr("opacity", 0);
    d3.selectAll("#pic5").attr("opacity", 0);
    d3.selectAll(".builder-label").attr("opacity", 0);
    d3.selectAll("#middlepic").attr("opacity", 0);
  },
  methods: {
    update: function (n) {
      console.log(n);
      d3.selectAll(".label1").attr("opacity", 0);
      d3.selectAll(".label2").attr("opacity", 0);
      d3.selectAll(".label3").attr("opacity", 0);
      d3.selectAll(".label4").attr("opacity", 0);
      d3.selectAll(".label5").attr("opacity", 0);
      d3.selectAll(".builder-label").attr("opacity", 1);
      d3.select("#pic1")
        .attr("href", "img/circle-15.svg")
        .attr("height", 50)
        .attr("width", 50)
        .attr("x", 600)
        .attr("y", 100);
      d3.select("#pic2")
        .attr("href", "img/circle-15.svg")
        .attr("height", 50)
        .attr("width", 50)
        .attr("x", 330)
        .attr("y", 270);
      d3.select("#pic3")
        .attr("href", "img/circle-15.svg")
        .attr("height", 50)
        .attr("width", 50)
        .attr("x", 380)
        .attr("y", 600);
      d3.select("#pic4")
        .attr("href", "img/circle-15.svg")
        .attr("height", 50)
        .attr("width", 50)
        .attr("x", 820)
        .attr("y", 600);
      d3.select("#pic5")
        .attr("href", "img/circle-15.svg")
        .attr("height", 50)
        .attr("width", 50)
        .attr("x", 870)
        .attr("y", 270);

      switch (n) {
        case 1:
          d3.select("#builder1").attr("opacity", 0);
          d3.select("#middlepic").attr("href", "img/boba_2.png");
          d3.select("#pic1")
            .attr("href", "img/tea_large.png")
            .attr("height", 150)
            .attr("width", 150)
            .attr("x", 550)
            .attr("y", 50);
          d3.selectAll(".label1").attr("opacity", 1);

          d3.select("#arrow-button")
            .style("top", 2950 + 2590 + "px")
            .style("left", "680px")
            .style("transform", "rotate(0deg)");

          this.step = 2;
          break;
        case 2:
          d3.select("#builder2").attr("opacity", 0);
          d3.select("#middlepic").attr("href", "img/boba_3.png");
          d3.select("#pic2")
            .attr("href", "img/milk_large.png")
            .attr("height", 150)
            .attr("width", 150)
            .attr("x", 280)
            .attr("y", 220);
          d3.selectAll(".label2").attr("opacity", 1);

          d3.select("#arrow-button")
            .style("top", 3300 + 2590 + "px")
            .style("left", "550px")
            .style("transform", "rotate(-60deg)");

          this.step = 3;
          break;
        case 3:
          d3.select("#builder3").attr("opacity", 0);
          d3.select("#middlepic").attr("href", "img/boba_4.png");
          d3.select("#pic3")
            .attr("href", "img/bubble_large.png")
            .attr("height", 150)
            .attr("width", 150)
            .attr("x", 330)
            .attr("y", 550);
          d3.selectAll(".label3").attr("opacity", 1);

          d3.select("#arrow-button")
            .style("top", 3550 + 2590 + "px")
            .style("left", "900px")
            .style("transform", "rotate(-140deg)");

          this.step = 4;
          break;
        case 4:
          d3.select("#builder4").attr("opacity", 0);
          d3.select("#middlepic").attr("href", "img/boba_5.png");
          d3.select("#pic4")
            .attr("href", "img/sweet_large.png")
            .attr("height", 150)
            .attr("width", 150)
            .attr("x", 770)
            .attr("y", 550);
          d3.selectAll(".label4").attr("opacity", 1);

          d3.select("#arrow-button")
            .style("top", 3250 + 2590 + "px")
            .style("left", "1250px")
            .style("transform", "rotate(140deg)");

          this.step = 5;
          break;
        case 5:
          d3.select("#builder5").attr("opacity", 0);
          d3.select("#middlepic").attr("href", "img/boba_6.png");
          d3.select("#pic5")
            .attr("href", "img/straw_large.png")
            .attr("height", 150)
            .attr("width", 150)
            .attr("x", 820)
            .attr("y", 220);
          d3.selectAll(".label5").attr("opacity", 1);

          d3.select("#arrow-button")
            .style("top", 2900 + 2590 + "px")
            .style("left", "1050px")
            .style("transform", "rotate(70deg)");

          this.step = 1;
          break;
      }
    },
    buttonclick: function () {
      this.update(this.step);
    },
  },
};
</script>

<style>
.builder-label {
  font-family: Neue Machina;
  font-style: normal;
  font-weight: 800;
  font-size: 24px;
  line-height: 23px;
  display: flex;
  align-items: center;
  text-align: center;
}

.builder-text-large {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
}

.builder-text {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
}

.label1 {
  transition: opacity 0.25s ease-in-out;
  -moz-transition: opacity 0.25s ease-in-out;
  -webkit-transition: opacity 0.25s ease-in-out;
}

.label2 .label3 .label4 .label5 {
  opacity: 0;

  transition: opacity 0.8s ease-in-out;
  -moz-transition: opacity 0.8s ease-in-out;
  -webkit-transition: opacity 0.8s ease-in-out;
}

#selection-1 {
  position: fixed;
  opacity: 0;
  top: 120px;
  left: 1020px;
  height: 50px;
  width: 50px;
  background-color: black;
  z-index: 50;
}
#selection-2 {
  position: fixed;
  opacity: 0;
  top: 290px;
  left: 750px;
  height: 50px;
  width: 50px;
  background-color: black;
  z-index: 50;
}
#selection-3 {
  position: fixed;
  opacity: 0;
  top: 620px;
  left: 800px;
  height: 50px;
  width: 50px;
  background-color: black;
  z-index: 50;
}
#selection-4 {
  position: fixed;
  opacity: 0;
  top: 620px;
  left: 1240px;
  height: 50px;
  width: 50px;
  background-color: black;
  z-index: 50;
}
#selection-5 {
  position: fixed;
  opacity: 0;
  top: 290px;
  left: 1290px;
  height: 50px;
  width: 50px;
  background-color: black;
  z-index: 50;
}

#selection-1:hover
  #selection-2:hover
  #selection-3:hover
  #selection-4:hover
  #selection-5:hover {
  cursor: pointer;
}

#arrow-button {
  position: absolute;
  left: 680px;
  top: 2950px;
  background-color: #fff7eb;
  border-style: none;
}

#arrow-button:hover {
  cursor: pointer;
}

#arrow {
  width: 50px;
  height: 50px;
  transform: rotate(135deg);
}
</style>
