<template>
  <div style="width: 95vw; overflow: hidden">
    <el-col :span="5" :offset="1" style="margin-top: 450px; position: fixed">
      <p class="treewords">
        <span class="bigemphasis">5,350</span> posts of the most recent 10,000
        posts for #boba
      </p>
      <p class="treewords">
        Randomly selected 400 posts from NYC <br />each color combination
        represent one post
      </p>
    </el-col>
    <el-col :span="13" :offset="13">
      <el-row
        ><span class="color-header">{{ currentHeader }}</span></el-row
      ><el-row>
        <span class="color-subheader"
          >boba shared on IG gets a lot of
          <span class="coloremphasis">
            <el-button class="coloremphasis" type="text" v-on:click="likes()"
              >likes</el-button
            ></span
          >
          for its rich
          <span class="coloremphasis">
            <el-button class="coloremphasis" type="text" v-on:click="colors()"
              >colors</el-button
            ></span
          ></span
        ></el-row
      >
    </el-col>
    <div id="colorpicker"></div>
    <el-button
      icon="el-icon-arrow-right"
      v-on:click="$router.push('/Bobavue/LDA-1')"
      class="navi-button"
    ></el-button>
    <el-button
      icon="el-icon-arrow-left"
      v-on:click="$router.push('/Bobavue/Menus')"
      class="navi-button-left"
    ></el-button>
  </div>
  <img src="img/mostlikes.png" class="static-image" />
</template>

<script>
import * as Vibrant from "node-vibrant";
import * as d3 from "d3";

let svg, chartsvg, div;
let imgdata = [];
let colorselect = {
  Orange: [],
  Red: [],
  Yellow: [],
  Gray: [],
  Green: [],
  Blue: [],
  Magenta: [],
  Cyan: [],
};
let pictures;

const colorscale = {
  Gray: "#4F4F4F",
  Red: "#784A44",
  Orange: "#C57650",
  Yellow: "#EA9D49",
  Green: "#4F7A70",
  Cyan: "#84BDBE",
  Blue: "#8F93D8",
  Magenta: "#EC9FFF",
};

function getColor(r, g, b) {
  console.log("running getColor");

  function mapMinMax(value, oldMin, oldMax, newMin, newMax) {
    return ((newMax - newMin) * (value - oldMin)) / (oldMax - oldMin) + newMin;
  }

  (r /= 255), (g /= 255), (b /= 255);

  var max = Math.max(r, g, b),
    min = Math.min(r, g, b);
  var h,
    s,
    l = (max + min) / 2;

  if (max == min) {
    h = s = 0; // achromatic
  } else {
    var d = max - min;
    s = l > 0.5 ? d / (2 - max - min) : d / (max + min);

    switch (max) {
      case r:
        h = (g - b) / d + (g < b ? 6 : 0);
        break;
      case g:
        h = (b - r) / d + 2;
        break;
      case b:
        h = (r - g) / d + 4;
        break;
    }

    h /= 6;
  }
  h = mapMinMax(h, 0, 1, 0, 360);

  if (l < 0.12) return ["Gray", h, s, l];
  if (l > 0.98) return ["Gray", h, s, l];
  if (s < 0.15) return ["Gray", h, s, l];
  if (h < 15) return ["Red", h, s, l];
  if (h < 45) return ["Orange", h, s, l];
  if (h < 65) return ["Yellow", h, s, l];
  if (h < 170) return ["Green", h, s, l];
  if (h < 190) return ["Cyan", h, s, l];
  if (h < 270) return ["Blue", h, s, l];
  if (h < 335) return ["Magenta", h, s, l];

  return "Red";
}

export default {
  data() {
    return {
      currentHeader: "Reason 2: Let's post on social media!",
      left: "disabled",
      right: "colors",
    };
  },
  mounted() {
    d3.select("#title").html(
      "<img src='img/reason_2.png' style='height: 60px; width: 280px;'>"
    );

    d3.csv("ins_final.csv", function (d) {
      return {
        image_path: d.purl,
        likes: d.likes_count,
        order: d.order,
      };
    }).then((data) => {
      pictures = data;
      console.log(pictures);

      imgdata = [];
      colorselect = {
        Orange: [],
        Red: [],
        Yellow: [],
        Gray: [],
        Green: [],
        Blue: [],
        Magenta: [],
        Cyan: [],
      };

      pictures.forEach(function (img) {
        Vibrant.from(img.image_path).getPalette(function (err, palette) {
          if (typeof palette !== "undefined") {
            let colors_array = [];
            let colors = [];
            for (let swatch of Object.entries(palette)) {
              colors.push(swatch[0]);
              colors_array.push(swatch[1]._population);
            }

            let top3 = [];
            let pop3 = [];
            for (let i = 0; i < 3; i++) {
              let largest = 0;
              let largest_i = 0;

              colors_array.forEach(function (d, i) {
                if (d > largest) {
                  largest = d;
                  largest_i = i;
                }
              });

              top3[i] = colors[largest_i];
              pop3[i] = colors_array[largest_i];
              colors = colors.filter((val) => val !== colors[largest_i]);
              colors_array = colors_array.filter((val) => val !== largest);
            }

            let swatch1 = top3[0];
            let swatch2 = top3[1];
            let swatch3 = top3[2];

            let color1 =
              "rgba(" +
              palette[swatch1]._rgb[0] +
              ", " +
              palette[swatch1]._rgb[1] +
              ", " +
              palette[swatch1]._rgb[2] +
              ", 1)";
            let color2 =
              "rgba(" +
              palette[swatch2]._rgb[0] +
              ", " +
              palette[swatch2]._rgb[1] +
              ", " +
              palette[swatch2]._rgb[2] +
              ", 0.5)";
            let color3 =
              "rgba(" +
              palette[swatch3]._rgb[0] +
              ", " +
              palette[swatch3]._rgb[1] +
              ", " +
              palette[swatch3]._rgb[2] +
              ", 0.5)";

            imgdata.push([
              img.image_path,
              img.likes,
              color1,
              color2,
              color3,
              img.order,
            ]);
            imgdata.sort(function (a, b) {
              return b[1] - a[1];
            });

            let color = getColor(
              palette[swatch1]._rgb[0],
              palette[swatch1]._rgb[1],
              palette[swatch1]._rgb[2]
            );

            colorselect[color[0]].push([
              color1,
              color2,
              color3,
              img.image_path,
              img.order,
              color[1],
              color[2],
              color[3],
            ]);
          }
        });
      });

      svg = d3
        .select("#colorpicker")
        .append("svg")
        .attr("height", 750)
        .attr("width", 1600);

      chartsvg = d3
        .select("#barchart")
        .append("svg")
        .attr("height", 800)
        .attr("width", 300);

      div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip")
        .style("opacity", 0);

      setTimeout(() => {
        this.treeMap();
      }, 10000);
    });
  },
  methods: {
    treeMap: function () {
      console.log("running treemap()");

      d3.selectAll("rect").remove();
      d3.selectAll("text").remove();

      function sumBySize(d) {
        return d.size;
      }

      function sumByCount(d) {
        return d.children ? 0 : 1;
      }

      let treedata = {
        name: "colors",
        children: [],
      };

      Object.keys(colorselect).forEach(function (key) {
        treedata.children.push({ name: key, size: colorselect[key].length });
      });

      console.log(treedata);

      //let format = d3.format(",d");

      let treemap = d3
        .treemap()
        .tile(d3.treemapResquarify)
        .size([1000, 650])
        .round(true)
        .paddingInner(1);

      let root = d3
        .hierarchy(treedata)
        .eachBefore(function (d) {
          d.data.id = (d.parent ? d.parent.data.id + "." : "") + d.data.name;
        })
        .sum(sumBySize)
        .sort(function (a, b) {
          return b.height - a.height || b.value - a.value;
        });

      treemap(root);

      var cell = svg
        .selectAll("g")
        .data(root.leaves())
        .enter()
        .append("g")
        .attr("transform", function (d) {
          return "translate(" + d.x0 + "," + d.y0 + ")";
        });

      cell
        .append("rect")
        .attr("id", function (d) {
          return d.data.id;
        })
        .attr("width", function (d) {
          return d.x1 - d.x0;
        })
        .attr("height", function (d) {
          return d.y1 - d.y0;
        })
        .attr("fill", function (d) {
          //console.log(d.data.id);
          return colorscale[d.data.name];
        })
        .on("mousemove", function (e, d) {
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html(d.data.id.substring(7))
            .style("left", e.pageX + "px")
            .style("top", e.pageY - 28 + "px");
        })
        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
        });

      cell
        .append("clipPath")
        .attr("id", function (d) {
          return "clip-" + d.data.id;
        })
        .append("use")
        .attr("xlink:href", function (d) {
          return "#" + d.data.id;
        });
      /* 
      cell
        .append("text")
        .attr("clip-path", function (d) {
          return "url(#clip-" + d.data.id + ")";
        })
        .selectAll("tspan")
        .data(function (d) {
          return d.data.name.split(/(?=[A-Z][^A-Z])/g);
        })
        .enter()
        .append("tspan")
        .attr("x", 10)
        .attr("y", function (d, i) {
          return 20 + i * 10;
        })
        .text(function (d) {
          return d;
        });

      cell.append("title").text(function (d) {
        return d.data.id + "\n" + format(d.value);
      }); */

      d3.selectAll("input")
        .data([sumBySize, sumByCount], function (d) {
          return d ? d.name : this.value;
        })
        .on("change", changed);

      var timeout = d3.timeout(function () {
        d3.select('input[value="sumByCount"]')
          .property("checked", true)
          .dispatch("change");
      }, 2000);

      function changed(sum) {
        timeout.stop();

        treemap(root.sum(sum));

        cell
          .transition()
          .duration(750)
          .attr("transform", function (d) {
            return "translate(" + d.x0 + "," + d.y0 + ")";
          })
          .select("rect")
          .attr("width", function (d) {
            return d.x1 - d.x0;
          })
          .attr("height", function (d) {
            return d.y1 - d.y0;
          });
      }

      let py = 195;
      let px = 1050;

      Object.keys(colorscale).forEach(function (key) {
        svg
          .append("circle")
          .attr("class", "legend")
          .attr("cx", px)
          .attr("cy", py)
          .attr("r", 10)
          .attr("fill", colorscale[key]);

        svg
          .append("text")
          .attr("class", "legend")
          .attr("x", px + 25)
          .attr("y", py + 5)
          .attr("class", "color-legend-text")
          .text(key);

        py += 35;
      });

      this.left = "disabled";
      this.right = "colors";
    },
    likes: function () {
      d3.select(".static-image").style("opacity", 1);
      console.log("running likes()");

      d3.selectAll("rect").remove();
      d3.selectAll("text").remove();

      d3.select("#colorpicker").style("margin-left", "400px");

      chartsvg.attr("opacity", 0);

      d3.selectAll(".legend").attr("opacity", 0);
      document
        .getElementsByClassName("treewords")
        .forEach((word) => (word.style.opacity = 0));

      let pos_y = 10;
      for (let i = 0; i < Math.floor(imgdata.length / 25); i++) {
        let pos_x = 0;
        for (let j = 0; j < 25; j++) {
          let myData = {
            img: imgdata[i * 25 + j][0],
            likes: imgdata[i * 25 + j][1],
          };

          console.log(myData);
          svg
            .append("rect")
            .attr("x", pos_x)
            .attr("y", pos_y)
            .attr("width", 30)
            .attr("height", 13)
            .attr("fill", imgdata[i * 25 + j][4])
            .on("mousemove", function (e) {
              div.transition().duration(200).style("opacity", 0.9);
              div
                .html(
                  "Likes: " +
                    myData.likes +
                    "<img src=" +
                    myData.img +
                    " width = '150' height='200'>"
                )
                .style("left", e.pageX + "px")
                .style("top", e.pageY - 200 + "px");
            })
            .on("mouseout", function () {
              div.transition().duration(500).style("opacity", 0);
            });

          svg
            .append("rect")
            .attr("x", pos_x + 2.5)
            .attr("y", pos_y + 13)
            .attr("width", 25)
            .attr("height", 25)
            .attr("fill", imgdata[i * 25 + j][2])
            .attr("rx", 1)
            .attr("ry", 1)
            .on("mousemove", function (e) {
              div.transition().duration(200).style("opacity", 0.9);
              div
                .html(
                  "Likes: " +
                    myData.likes +
                    "<img src=" +
                    myData.img +
                    " width = '150' height='200'>"
                )
                .style("left", e.pageX + "px")
                .style("top", e.pageY - 200 + "px");
            })
            .on("mouseout", function () {
              div.transition().duration(500).style("opacity", 0);
            });

          svg
            .append("rect")
            .attr("x", pos_x + 3.5)
            .attr("y", pos_y + 38)
            .attr("width", 23)
            .attr("height", 25)
            .attr("fill", imgdata[i * 25 + j][3])
            .attr("rx", 1)
            .attr("ry", 1)
            .on("mousemove", function (e) {
              div.transition().duration(200).style("opacity", 0.9);
              div
                .html(
                  "Likes: " +
                    myData.likes +
                    "<img src=" +
                    myData.img +
                    " width = '150' height='200'>"
                )
                .style("left", e.pageX + "px")
                .style("top", e.pageY - 200 + "px");
            })
            .on("mouseout", function () {
              div.transition().duration(500).style("opacity", 0);
            });

          pos_x += 45;
        }
        pos_y += 85;
      }

      this.left = "colors";
      this.right = "disabled";
    },
    colors: function () {
      console.log("running colors()");

      d3.select(".static-image").style("opacity", 0);
      d3.selectAll("rect").remove();
      d3.selectAll("text").remove();

      d3.select("#colorpicker").style("margin-left", "250px");

      d3.selectAll(".legend").attr("opacity", 0);
      chartsvg.attr("opacity", 1);

      let pos_y = 10;

      let total = 0;
      Object.keys(colorselect).forEach(function (key) {
        total += colorselect[key].length;
        console.log(colorselect[key]);
        colorselect[key].sort(function (a, b) {
          return b[7] - a[7];
        });
        console.log(colorselect[key]);
      });

      console.log(total);

      Object.keys(colorselect).forEach(function (key) {
        if (colorselect[key].length > 0) {
          let pos_x = 10;
          let size = colorselect[key].length;

          svg
            .append("text")
            .attr("x", pos_x)
            .attr("y", pos_y + 10)
            .attr("width", 75)
            .attr("height", 25)
            .attr("class", "color-label-top")
            .attr("fill", "#9f9f9f")
            .text(key.toLowerCase());

          svg
            .append("text")
            .attr("x", pos_x)
            .attr("y", pos_y + 40)
            .attr("width", 75)
            .attr("height", 25)
            .attr("class", "color-label-bot")
            .text(Math.round(100 * (size / total)) + "%");

          pos_x += 100;

          svg
            .append("rect")
            .attr("x", pos_x)
            .attr("y", pos_y)
            .attr("width", 1152)
            .attr("height", 40)
            .attr("fill", "#FFF5E9")
            .attr("stroke", "#E2DAC8")
            .attr("stroke-width", 1)
            .attr("stroke-dasharray", 2, 2);

          let w = 15;
          if (size > 80) {
            w = 12;
          }

          for (let i = 0; i < size; i++) {
            let myData = {
              img: colorselect[key][i][3],
              order: colorselect[key][i][4],
            };

            svg
              .append("rect")
              .attr("x", pos_x)
              .attr("y", pos_y)
              .attr("width", w)
              .attr("height", 10)
              .attr("fill", colorselect[key][i][2])
              .on("mousemove", function (e) {
                div.transition().duration(200).style("opacity", 0.9);
                div
                  .html(
                    myData.order +
                      "<img src=" +
                      myData.img +
                      " width = '150' height='200'>"
                  )
                  .style("left", e.pageX + "px")
                  .style("top", e.pageY - 200 + "px");
              })
              .on("mouseout", function () {
                div.transition().duration(500).style("opacity", 0);
              });

            svg
              .append("rect")
              .attr("x", pos_x)
              .attr("y", pos_y + 10)
              .attr("width", w)
              .attr("height", 20)
              .attr("fill", colorselect[key][i][0])
              .attr("rx", 1)
              .attr("ry", 1)
              .on("mousemove", function (e) {
                div.transition().duration(200).style("opacity", 0.9);
                div
                  .html(
                    myData.order +
                      "<img src=" +
                      myData.img +
                      " width = '150' height='200'>"
                  )
                  .style("left", e.pageX + "px")
                  .style("top", e.pageY - 200 + "px");
              })
              .on("mouseout", function () {
                div.transition().duration(500).style("opacity", 0);
              });

            svg
              .append("rect")
              .attr("x", pos_x)
              .attr("y", pos_y + 30)
              .attr("width", w)
              .attr("height", 10)
              .attr("fill", colorselect[key][i][1])
              .attr("rx", 1)
              .attr("ry", 1)
              .on("mousemove", function (e) {
                div.transition().duration(200).style("opacity", 0.9);
                div
                  .html(
                    myData.order +
                      "<img src=" +
                      myData.img +
                      " width = '150' height='200'>"
                  )
                  .style("left", e.pageX + "px")
                  .style("top", e.pageY - 200 + "px");
              })
              .on("mouseout", function () {
                div.transition().duration(500).style("opacity", 0);
              });
            pos_x += w;
          }
          pos_y += 85;
        }
      });

      document
        .getElementsByClassName("treewords")
        .forEach((word) => (word.style.opacity = 0));

      /*       let px = 50;
      let py = 175;

      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 100)
        .text("Color by major")
        .attr("id", "headerbar");
      chartsvg
        .append("text")
        .attr("x", px)
        .attr("y", 125)
        .text("categories")
        .attr("id", "headerbar");

      Object.keys(colorselect).forEach(function (key) {
        chartsvg.append("text").attr("x", px).attr("y", py).text(key);

        chartsvg
          .append("rect")
          .attr("x", px)
          .attr("y", py + 20)
          .attr("height", 25)
          .attr(
            "width",
            () => 300 * (colorselect[key].length / colorselect.Orange.length)
          )
          .attr("fill", () => colorscale[key]);

        py += 70; 
      });*/

      this.left = "treemap";
      this.right = "likes";
    },
    leftButton: function () {
      switch (this.left) {
        case "disabled":
          console.log("disabled button");
          break;
        case "treemap":
          this.treeMap();
          break;
        case "colors":
          this.colors();
          break;
        case "likes":
          this.likes();
          break;
      }
    },
    rightButton: function () {
      switch (this.right) {
        case "disabled":
          console.log("disabled button");
          break;
        case "treemap":
          this.treeMap();
          break;
        case "colors":
          this.colors();
          break;
        case "likes":
          this.likes();
          break;
      }
    },
  },
};
</script>

<style>
#maincaption {
  text-align: left;
}

.navi-button {
  position: absolute;
  left: 94%;
  right: 20.62%;
  top: 0%;
  bottom: 0%;
  background-color: #fff7eb;
  border: none;
  font-size: 3rem;
  color: #e2dac8;
}

.navi-button-left {
  position: absolute;
  left: 0%;
  top: 0%;
  width: 0px;
  bottom: 0%;
  background-color: #fff7eb;
  border: none;
  font-size: 3rem;
  color: #e2dac8;
}
.navi-button:hover {
  color: #e2dac8;

  background-color: #fff7eb;
}
.navi-button:focus {
  color: #e2dac8;

  background-color: #fff7eb;
}
.navi-button-left:hover {
  color: #e2dac8;

  background-color: #fff7eb;
}
.navi-button-left:focus {
  color: #e2dac8;

  background-color: #fff7eb;
}

#colorpicker {
  position: fixed;
  margin-top: 200px;
  margin-left: 500px;
}

#preview {
  height: 400px;
  width: 300px;
  border-radius: 15px;
}

.light-emphasis {
  font-family: "Syne Mono", monospace;
  color: #c4c4c4;
}

.treewords {
  font-family: Montserrat;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;
  width: 366px;
}

.bigemphasis {
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
}

.coloremphasis {
  color: #e2dac8;
  font-weight: bold;
  font-size: 18px;
}

.coloremphasis:hover {
  color: #c57650;
}

.coloremphasis:focus {
  color: #c57650;
}

.color-header {
  width: 100%;
  margin-top: 80px;
  margin-bottom: 50px;
  font-family: Montserrat;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  text-align: right;
}

.color-subheader {
  width: 100%;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  margin-top: -40px;
  margin-bottom: 30px;
}

.static-image {
  position: fixed;
  top: 220px;
  left: 300px;
  opacity: 0;
}

.tooltip {
  position: absolute;
  text-align: center;
  width: 150px;
  padding: 2px;
  font-family: "Neue Machina";
