<template>
  <div id="scroller">
    <div class="fullpage">
      <img class="landing" src="img/landing3.png" />
      <el-col :span="4" :offset="8">
        <div class="textbox builder-step" id="step-1">
          <p>
            <span class="quote"> “ </span>
            When we were young and so alive, Thinking memories the best times of
            my life, I want coffee when I'm waking up, Tequila to keep a good
            buzz. …… No matter how much I love other drinks, I keep coming back
            to you
            <br />
            <span class="highlight-black"
              >You were my first love,Boba. Love you forever,Boba</span
            >
            <span class="quote-back"> “ </span>
          </p>
          <div class="source">
            Boba (A Love Song), by Jason Chen, an Asian American Pop Singer
          </div>
        </div>
      </el-col>
    </div>
    <div class="fullpage">
      <el-col :span="4" :offset="8">
        <div class="textbox builder-step" id="step-2">
          <p>
            <span class="quote"> 🧋 </span>
            <br />

            <span class="highlight-black"
              >Bubble tea (often called Boba in the west coast)</span
            >
            <br />
            is a milk-based tea with tapioca pearls inside. Over the past 30
            years, It has gone from a niche Taiwanese beverage to a mainstream
            iconic drink for Asian Americans.
          </p>
        </div>
      </el-col>
    </div>
    <div class="fullpage">
      <el-col :span="4" :offset="8">
        <div class="textbox builder-step" id="step-3">
          <span class="highlight-black">Let's get boba</span
          ><img src="img/clickblanks.png" style="margin-left: 25px" />
          <p>
            In 2020, me and my roommate ordered
            <el-button
              class="intro-text-button"
              v-on:click="func5()"
              type="text"
              >100
            </el-button>
            cups from
            <el-button
              class="intro-text-button"
              v-on:click="func6()"
              type="text"
              >9</el-button
            >
            boba shops in lower manhattan.
            <el-button
              class="intro-text-button"
              v-on:click="func7()"
              type="text"
              >My roommate</el-button
            >
            is the one who drank more.
          </p>
        </div>
      </el-col>
      <el-col :span="12"> </el-col>
    </div>
  </div>
  <div class="fullpage builder-step" id="step-4"></div>
  <div class="fullpage builder-step" id="step-5">
    <button class="bobabut" v-on:click="func10()">
      <img src="img/notes.png" style="height: 25px; width: 25px" />
    </button>
    <div class="bobabut2">
      波霸 – Original a Taiwan slang for “big breasts” – came about when another
      tea shop used larger tapioca balls (about a quarter of an inch) compared
      to the original (one-twelfth of an inch)
    </div>
  </div>
  <img src="img/clicknumbers.png" class="fixed-num" />
  <div class="fullpage builder-step" id="step-6"></div>
  <BobaBuilder />
  <div class="fullpage builder-step" id="step-7"></div>
  <div>
    <Mapbox />
  </div>
  <div class="fullpage">
    <Nav />
  </div>
</template>

<script>
import BobaBuilder from "./BobaBuilder2.vue";
import Mapbox from "./Mapbox.vue";
import Nav from "./Navigation.vue";
import * as d3 from "d3";
import scrollama from "scrollama";

let simulation, nodes, svg;

let stores = {
  "Moge Tea (East Village)": [325, 100],
  "Kung Fu Tea": [625, 100],
  "Gong Cha": [925, 100],
  "Vivi Bubble Tea": [150, 350],
  "Tiger Sugar (Canal St)": [1100, 600],
  "KOI THA": [1100, 350],
  ShareTea: [325, 600],
  "Chun Yang Tea": [625, 600],
  "Yaya Tea": [925, 600],
};

const stores_colors = [
  "#855647",
  "#EA9D49",
  "#C57650",
  "#4F7A70",
  "#EC9FFF",
  "#F56E55",
  "#8555D9",
  "#EC9FFF",
  "#784A44",
];

const stores_Colorscale = d3.scaleOrdinal(Object.keys(stores), stores_colors);

export default {
  data() {
    return {
      opac: true,
      step: 1,
    };
  },
  components: {
    BobaBuilder,
    Mapbox,
    Nav,
  },
  mounted() {
    d3.csv("eats_order_details.csv", function (d) {
      return {
        shop: d.Restaurant,
        name: d.Name,
        item: d.ItemName,
        price: d.ItemPrice,
        custom: d.Customizations,
        randx: d.x,
        randy: d.y,
        smallx: d.smallx,
        smally: d.smally,
      };
    }).then((data1) => {
      svg = d3.select("#lifecycle");

      simulation = d3.forceSimulation(data1);
      simulation.on("tick", () => {
        nodes.attr("cx", (d) => d.x).attr("cy", (d) => d.y);
      });

      simulation.stop();

      nodes = svg
        .selectAll("circle")
        .data(data1)
        .enter()
        .append("circle")
        .attr("r", 15)
        .attr("cx", (d) => d.x)
        .attr("cy", (d) => d.y)
        .attr("opacity", 0.8)
        .attr("class", (d) => d.name);

      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("r", 15)
        .attr("fill", "#fff7eb")
        .attr("stroke", "black")
        .attr("stroke-width", "2");

      svg
        .append("image")
        .attr("id", "wordmap")
        .attr("width", 700)
        .attr("height", 570)
        .attr("x", 250)
        .attr("y", 150)
        .attr("href", "img/name1.png")
        .attr("opacity", 0);

      this.func1();

      var scroller = scrollama();

      scroller
        .setup({
          step: ".builder-step",
          offset: 0.5,
          progress: true,
        })
        .onStepEnter((response) => {
          switch (response.element.id) {
            case "step-1":
              this.func1();
              break;
            case "step-2":
              this.func2();
              break;
            case "step-3":
              this.func3();
              break;
            case "step-4":
              this.func8();
              break;
            case "step-5":
              this.func9();
              break;
            case "step-6":
              this.func11();
              break;
            case "step-7":
              this.func4();
              break;
          }
        });
    });
  },
  methods: {
    func1: function () {
      d3.select("#title").html("");

      d3.select("#graphic").style("opacity", 1);
      d3.select("#graphic").style("z-index", 1);

      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("r", 15)
        .attr("fill", "#fff7eb")
        .attr("stroke", "black")
        .attr("stroke-width", "2");

      simulation
        .force("charge", d3.forceManyBody().strength([-30]))
        .force(
          "forceX",
          d3.forceX((d) => d.smallx - 100)
        )
        .force(
          "forceY",
          d3.forceY((d) => d.smally - 200)
        )
        .force("collide", d3.forceCollide(18))
        .alphaDecay([0.01]);

      //Reheat simulation and restart
      simulation.alpha(0.3).restart();
    },

    func2: function () {
      d3.select("#title").html(
        "<img src='img/title_intro1.png' style='height: 60px; width: 342px;'>"
      );

      d3.select("#graphic").style("opacity", 1);
      d3.select("#graphic").style("z-index", 1);

      svg.selectAll("ellipse").attr("opacity", 0);

      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("r", 15)
        .attr("fill", "#fff7eb")
        .attr("stroke", "black")
        .attr("stroke-width", "2");

      simulation
        .force("charge", d3.forceManyBody().strength([-10]))
        .force(
          "forceX",
          d3.forceX((d) => d.randx - 200)
        )
        .force(
          "forceY",
          d3.forceY((d) => d.randy - 150)
        )
        .force("collide", d3.forceCollide(20))
        .alphaDecay([0.01]);

      //Reheat simulation and restart
      simulation.alpha(0.3).restart();
    },

    func3: function () {
      d3.select("#graphic").style("opacity", 1);
      d3.select("#graphic").style("z-index", 1);

      svg.selectAll("ellipse").attr("opacity", 1);

      svg.select("#wordmap").attr("opacity", 0);

      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("r", 15)
        .attr("fill", "#fff7eb")
        .attr("opacity", 1);

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force("forceX", d3.forceX(625))
        .force("forceY", d3.forceY(425))
        .force("collide", d3.forceCollide(18))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.4).restart();
    },

    func4: function () {
      d3.select("#title").html(
        "<img src='img/title_intro2.png' style='height: 60px; width: 342px;'>"
      );
      d3.select("#graphic").style("opacity", 0);
      d3.select("#graphic").style("z-index", -2);
      d3.select("#step-5").style("opacity", 0);
      d3.select("#map").style("z-index", 0);
      d3.selectAll(".selection").style("z-index", 0);
      d3.select(".fixed-num").style("opacity", 0);
      console.log(4);
    },

    func5: function () {
      svg.selectAll("ellipse").attr("opacity", 0.5);
      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("fill", "black");

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force("forceX", d3.forceX(625))
        .force("forceY", d3.forceY(425))
        .force("collide", d3.forceCollide(18))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.4).restart();
    },
    func6: function () {
      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("fill", (d) => stores_Colorscale(d.shop));

      svg.selectAll("ellipse").attr("opacity", 0);

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force(
          "forceX",
          d3.forceX((d) => stores[d.shop][0] - 100)
        )
        .force(
          "forceY",
          d3.forceY((d) => stores[d.shop][1] + 100)
        )
        .force("collide", d3.forceCollide(20))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.6).restart();
    },
    func7: function () {
      svg.selectAll("ellipse").attr("opacity", 0);
      svg
        .selectAll("circle")
        .transition()
        .duration(300)
        .delay((d, i) => i * 5)
        .attr("fill", function (d) {
          if (d.name == "JY") {
            return "#4F7A70";
          } else {
            return "#8555D9";
          }
        });

      simulation
        .force("charge", d3.forceManyBody().strength([0]))
        .force("forceX", d3.forceX(625))
        .force(
          "forceY",
          d3.forceY(function (d) {
            if (d.name == "JY") {
              return 200;
            } else {
              return 725;
            }
          })
        )
        .force("collide", d3.forceCollide(20))
        .alphaDecay([0.02]);

      //Reheat simulation and restart
      simulation.alpha(0.6).restart();
    },
    func8: function () {
      svg.select("#wordmap").attr("href", "img/name1.png").attr("opacity", 1);
      svg.selectAll("circle").attr("opacity", 0);
      d3.select("#graphic").style("opacity", 1);

      d3.select(".bobabut").style("opacity", 0);
    },
    func9: function () {
      d3.select("#title").html(
        "<img src='img/title_intro1.png' style='height: 60px; width: 342px;'>"
      );
      console.log(9);
      d3.selectAll(".selection").style("z-index", 0);
      svg.selectAll("ellipse").attr("opacity", 1);
      svg.select("#wordmap").attr("href", "img/name2.png").attr("opacity", 1);
      d3.select(".fixed-num").style("opacity", 0);
      svg.selectAll("circle").attr("opacity", 0);
      d3.select(".bobabut").style("opacity", 1);
      d3.select("#graphic").style("opacity", 1);
      d3.selectAll(".label1").attr("opacity", 0);
      d3.selectAll(".label2").attr("opacity", 0);
      d3.selectAll(".label3").attr("opacity", 0);
      d3.selectAll(".label4").attr("opacity", 0);
      d3.selectAll(".label5").attr("opacity", 0);
      d3.selectAll("#pic1").attr("opacity", 0);
      d3.selectAll("#pic2").attr("opacity", 0);
      d3.selectAll("#pic3").attr("opacity", 0);
      d3.selectAll("#pic4").attr("opacity", 0);
      d3.selectAll("#pic5").attr("opacity", 0);
      d3.selectAll(".builder-label").attr("opacity", 0);
      d3.selectAll("#middlepic").attr("opacity", 0);
    },
    func10: function () {
      d3.selectAll(".selection").style("z-index", 0);
      if (this.opac) {
        d3.select(".bobabut2").style("opacity", 1);
        this.opac = false;
      } else {
        d3.select(".bobabut2").style("opacity", 0);
        this.opac = true;
      }
    },
    func11: function () {
      d3.select("#title").html(
        "<img src='img/title_intro2.png' style='height: 60px; width: 342px;'>"
      );
      console.log(11);
      d3.selectAll(".selection").style("z-index", 50);
      d3.select(".fixed-num").style("opacity", 1);
      svg.selectAll("ellipse").attr("opacity", 1);
      d3.select(".bobabut").style("opacity", 0);
      d3.select(".bobabut2").style("opacity", 0);
      this.opac = true;
      d3.select("#graphic").style("opacity", 1);
      svg.select("#wordmap").attr("href", "img/name2.png").attr("opacity", 0);
      svg.selectAll("circle").attr("opacity", 0);
      d3.selectAll(".label1").attr("opacity", 1);
      d3.selectAll(".label2").attr("opacity", 0);
      d3.selectAll(".label3").attr("opacity", 0);
      d3.selectAll(".label4").attr("opacity", 0);
      d3.selectAll(".label5").attr("opacity", 0);
      d3.selectAll("#pic1").attr("opacity", 1);
      d3.selectAll("#pic2").attr("opacity", 1);
      d3.selectAll("#pic3").attr("opacity", 1);
      d3.selectAll("#pic4").attr("opacity", 1);
      d3.selectAll("#pic5").attr("opacity", 1);
      d3.selectAll(".builder-label").attr("opacity", 1);
      d3.selectAll("#middlepic").attr("opacity", 1);
      d3.select("#map").style("z-index", -1);
    },
  },
};
</script>

<style>
.textbox {
  margin-top: 45vh;
  width: 650px;
  padding: 40px;
  position: relative;
  background-color: #fff7eb;
  z-index: 10;
  text-align: left;
}

.fixed-num {
  position: fixed;
  top: 290px;
  left: 850px;
  opacity: 0;
}

.bobabut {
  position: fixed;
  background-color: #fff7eb;
  border-style: none;
  top: 640px;
  left: 1080px;
  z-index: 12;

  opacity: 0;
  transition: opacity 0.8s;
  -webkit-transition: opacity 0.8;
}

.bobabut2 {
  position: fixed;
  background-color: #fff7eb;
  top: 650px;
  left: 1120px;
  width: 300px;
  z-index: 12;
  text-align: left;

  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 15px;

  opacity: 0;
  transition: opacity 0.8s;
  -webkit-transition: opacity 0.8;
}

.bobabut:hover {
  cursor: pointer;
}

#graphic {
  position: fixed;
  top: 20px;
  left: 0;
  height: 100vh;
  width: 100vw;
  transition: opacity 0.8s;
  -webkit-transition: opacity 0.8;
  z-index: 0;
  opacity: 0;
}

#wordmap {
  transition: opacity 0.8s;
  -webkit-transition: opacity 0.8;
}

#step-3 {
  width: 650px;
}

.intro-text-button {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 12px;
  text-align: right;

  padding: 5px;
  background-color: #fff5e9;
  color: #fff5e9;
  border-width: 1px;
  border-color: #e2dac8;
  border-style: solid;
}

.blue:hover {
  color: #8f93d8 !important;
}

.landing {
  position: absolute;
  top: 0px;
  left: 0;
  height: 100%;
  width: 100%;
}

.highlight-black {
  background-color: black;
  color: #fff7eb;
  padding: 5px 20px 5px;
  font-family: "Neue Machina Bold";
  font-size: 21px;
  line-height: 1.5;
}
.quote {
  font-family: "Neue Machina Bold";
  font-size: 36px;
  color: #e2dac8;
  padding-top: 20px;
}
.quote-back {
  font-family: "Neue Machina Bold";
  font-size: 36px;
  text-align: right;
  color: #e2dac8;
  padding-top: 30px;
  padding-bottom: 20px;
}
.source {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 15px;
  font-style: italic;
  text-align: right;
}
.splash {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-image: url("../../public/img/0preview.png");
  background-color: #c57650;
  background-repeat: no-repeat;
  z-index: 0;
}
</style>
